
const getMyProfile = async () => {
  const result = await API.graphql({
    query: gql`
      query getMyProfile {
        getMyProfile {
          id
          username
          avatar
          background_image
          bio
          location
          website
          birthdate
          created_at
          followers_num
          following_num
          tweets_num
          likes_num
        }
      }
    `,
    authMode: process.env.VUE_APP_APPSYNC_AUTHENTICATION_TYPE,
  });
  const profile = result.data.getMyProfile;

  profile.avatar = profile.avatar || "default_profile.png";
  return profile;
};

const getProfileByusername = async (username) => {
  const result = await API.graphql({
    query: gql`
      query getProfile($username: String!) {
        getProfile(username: $username) {
          id
          name
          username
          avatar
          background_image
          bio
          location
          website
          birthdate
          created_at
          followers_num
          following_num
          tweets_num
          likes_num
          following
          followedBy
        }
      }
    `,
    variables: {
      username,
    },
    authMode: process.env.VUE_APP_APPSYNC_AUTHENTICATION_TYPE,
  });
  const profile = result.data.getProfile || {};

  profile.avatar = profile.avatar || "default_profile.png";
  return profile;
};

const getMyTimeline = async (limit, nextToken) => {
  const result = await API.graphql({
    query: gql`
      query getMyTimeline($limit: Int!, $nextToken: String) {
        getMyTimeline(limit: $limit, nextToken: $nextToken) {
          nextToken
          tweets {
            __typename
            id
            profile {
              id
              name
              username
              avatar
            }
            created_at
            ... on Tweet {
              text
              liked
              likes
              retweeted
              retweets
              replies
            }
            ... on Retweet {
              retweetOf {
                id
                profile {
                  id
                  name
                  username
                  avatar
                }
                created_at
                ... on Tweet {
                  text
                  liked
                  likes
                  retweeted
                  retweets
                  replies
                }
                ... on Reply {
                  text
                  liked
                  likes
                  retweeted
                  retweets
                  replies
                }
              }
            }
            ... on Reply {
              text
              liked
              likes
              retweeted
              retweets
              replies
              inReplyToTweet {
                id
                profile {
                  id
                  name
                  username
                  avatar
                }
                created_at
                ... on Tweet {
                  text
                  liked
                  likes
                  retweeted
                  retweets
                  replies
                }
                ... on Reply {
                  text
                  liked
                  likes
                  retweeted
                  retweets
                  replies
                }
                ... on Retweet {
                  retweetOf {
                    id
                  }
                }
              }
              inReplyToUsers {
                id
                name
                username
                avatar
              }
            }
          }
        }
      }
    `,
    variables: {
      limit,
      nextToken,
    },
    authMode: process.env.VUE_APP_APPSYNC_AUTHENTICATION_TYPE,
  });

  return result.data.getMyTimeline;
};

