const getOnNotifiedSubscription = (userId) => {
  const onNotified = {
    query: gql`
      subscription onNotified($userId: ID!) {
        onNotified(userId: $userId) {
          ... on Retweeted {
            id
            created_at
            retweetedBy
            tweetId
            retweetId
            type
          }
          ... on Liked {
            id
            created_at
            likedBy
            tweetId
            type
          }
          ... on Mentioned {
            id
            created_at
            mentionedBy
            mentionedByTweetId
            type
          }
          ... on Replied {
            id
            created_at
            repliedBy
            tweetId
            replyTweetId
            type
          }
          ... on DMed {
            id
            message
            created_at
            otherUserId
            type
          }
        }
      }
    `,
    variables: {
      userId: userId,
    },
  };

  return API.graphql(onNotified);
};

